<template>
  <div id="app">
    <h1>父组件</h1>
    <div class="cons">
      <p>边框颜色:</p>
      <input type="color" id="one" :value="oneColor" @blur="oneF" ref="myOne" />
      <p>填充颜色：</p>
      <input type="color" id="two" :value="twoColor" @blur="twoF" ref="myTwo" />
      <p>边框宽度：</p>
      <input type="range" @input="rang" ref="myRang" value="10" />
    </div>
    <hr />
    <subcomponents :strokeColor="oneColor" :fillColor="twoColor" :strokeWidth="rangLen"></subcomponents>
  </div>
</template>

<script>
import Subcomponents from '@/components/Subcomponents.vue'
export default {
  name: 'App',
  components: {
    Subcomponents
  },
  data() {
    return {
      oneColor: '#00ffff',
      twoColor: '#FFA500',
      rangLen: '10px'
    }
  },
  methods: {
    /* 边框的颜色 */
    oneF() {
      const s = this.$refs.myOne.value
      this.oneColor = s
    },
    /* 填充颜色 */
    twoF() {
      const s = this.$refs.myTwo.value
      this.twoColor = s
    },
    /* 边框的宽度 */
    rang() {
      const s = this.$refs.myRang.value
      this.rangLen = s
    }
  }
}
</script>

<style lang="less" scoped>
#app {
  margin: 10px 20px;
  padding: 20px;
  background-color: #cdcdcd;
  input[type='text'] {
    outline: none;
    padding: 2px 0 2px 5px;
  }

  /deep/ hr {
    margin: 10px 0;
  }
}
</style>
